.about-section {
  padding-top: 61px;
  padding-left: 55px;
  padding-right: 55px;
}

.about-title-wrapper {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.about-title {
  font-size: var(--font-size-medium-small);
  color: var(--white-shade);
}

.about-title-highlighted {
  color: var(--white-shade);
}

.about-content-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding-top: 54px;
  padding-bottom: 50px;
  display: flex;
}

.about-content-image {
  box-shadow: 0 -25.88px var(--yellow);
  border-radius: 165px;
  margin-top: 25.88px;
}

.about-text-wrapper {
  flex-direction: column;
  gap: 4px;
  max-width: 220px;
  padding-bottom: 24px;
  display: flex;
}

.about-text {
  color: var(--white-shade);
  font-size: var(--font-size-xs);
  letter-spacing: .48px;
  text-align: center;
  line-height: 21.12px;
}

.about_button {
  background-color: var(--yellow);
  font-weight: 600;
  font-size: var(--font-size-small);
  border: none;
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  gap: 4px;
  max-width: 164px;
  margin: 0 auto;
  padding: 12px 32px;
  display: flex;
}

@media screen and (width >= 480px) {
  .about-content-wrapper {
    padding-bottom: 67px;
  }
}

@media screen and (width >= 768px) {
  .about-content-wrapper {
    flex-direction: row;
    gap: 38px;
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .about-content-image {
    width: 330px;
    height: 330px;
  }

  .about-text-wrapper {
    gap: 8px;
    max-width: 334px;
    padding-bottom: 32px;
  }

  .about-text {
    text-align: left;
  }

  .about_button {
    margin: 0;
  }
}

@media screen and (width >= 1200px) {
  .about-content-wrapper {
    gap: 109px;
    padding-top: 84px;
    padding-bottom: 67px;
  }

  .about-content-image {
    box-shadow: 0 -25.88px var(--yellow);
    border-radius: 250px;
    width: 490px;
    height: 490px;
    margin-top: 25.88px;
  }

  .about-text-wrapper {
    gap: 0;
    max-width: 463px;
    padding-bottom: 54px;
  }

  .about-text {
    font-size: var(--font-size-normal);
    letter-spacing: .56px;
    line-height: 27.16px;
  }

  .about-text-first {
    padding-bottom: 8px;
  }
}
